@import './variables';

.Contact {
    height: 100vh;
    width: 100%;
    background: $background-color;
    font-size: 1.7em;
}

.contact__first {
    display: flex;
    height: 70%;
    position: relative;
    align-items: center;
    justify-content: center;
}

.contact__first-background {
    position: absolute;
    background: url(../imgs/Background-Website.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    width: 120%;
    left: 0;
    &::before {
        position: absolute;
        width: 0;
        height: 0;
        content: '';
        border-top: 14em solid $background-color;
        border-left: 16em solid transparent;
        right: 0;
    }
    &::after {
        position: absolute;
        width: 0;
        height: 0;
        content: '';
        border-bottom: 14em solid $background-color;
        border-left: 16em solid transparent;
        right: 0;
        bottom: 0;
    }
}

.textContainer {
    z-index: 1;
}

.contact__second {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    z-index: 1;
    box-sizing: border-box;
    padding: 0 0 0 3em;
}

.contact__iphone {
    height: 79%;
    border: 10px solid black;
    aspect-ratio: 9/19.5;
    border-radius: 50px;
    background: $accent-color;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
}

.contact__form-wrapper {
    width: 100%;
}

.contact__form {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    padding: 3em 0.6em 0.6em;
    position: relative;
    align-items: center;
}

.contact__iphone-notch {
    position: absolute;
    background: $background-color;
    height: 1.4em;
    width: 8em;
    top: 0;
    border-radius: 0 0 3em 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}

.contact__iphone-notch-sensor {
    height: 0.3em;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background-color: black;
}

.contact__iphone-notch-speaker {
    height: 0.2em;
    width: 2em;
    background-color: black;
    border-radius: 5em;
}

.contact__iphone-notch-camera {
    height: 0.5em;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background-color: black;
}

.contact__form-input-label {
    position: relative;
    display: flex;
    align-items: center;
}

.contact__form-input-icon {
    position: absolute;
    left: 0;
    box-sizing: border-box;
    background: #2e7045;
    border-radius: 0.7em 0.3em;
    height: 100%;
    aspect-ratio: 1.1/1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $white-font-color;
}

.contact__form-item {
    width: 100%;
    padding: 1em;
    font-size: 1em;
}

.contact__input {
    border-radius: 20px;
    height: 2.5em;
    box-sizing: border-box;
    text-align: center;
    border: none;
}

.contact__textarea {
    aspect-ratio: 1/1.2;
    border-radius: 25px;
    box-sizing: border-box;
    font-size: 1em;
    font-family: 'Roboto', sans-serif;
    border: none;
}

.contact__textarea::placeholder {
    font-family: 'Roboto', sans-serif;
}

.contact__submit-button {
    position: absolute;
    box-sizing: border-box;
    padding: 1em;
    border: none;
    background: #2e7045;
    color: $white-font-color;
    border-radius: 1.5em 0.5em;
    width: 30%;
}

.contact__icon {
    width: 100%;
    height: 100%;
    aspect-ratio: 1/1;
}

@media only screen and (max-width: 300px) {
    .Contact {
        font-size: 0.65em;
        display: grid;
        grid-auto-flow: row;
        grid-auto-rows: 50%;
    }

    .contact__first {
        height: 100%;
    }

    .contact__first-background {
        &::before {
            position: absolute;
            width: 0;
            height: 0;
            content: '';
            border-top: 14em solid transparent;
            border-left: 16em solid transparent;
            right: 0;
        }
        &:after {
            position: absolute;
            width: 0;
            height: 0;
            content: '';
            border-bottom: 14em solid transparent;
            border-left: 16em solid transparent;
            right: 0;
        }
    }

    .contact__text-container {
        max-width: 240px;
    }

    .contact__second {
        align-items: center;
        padding: 0;
        position: relative;
    }

    .contact__iphone {
        position: absolute;
        top: -60px;
        border-radius: 20px;
        border: 4px solid black;
    }

    .contact__textarea {
        border-radius: 10px;
    }

    .contact__submit-button {
        padding: 0.8em;
        border-radius: 1em 0.3em;
    }
}

